import React, { Component } from 'react';
import { stories } from './data/stories.js';
import { Navigation } from './components/Navigation.js';
import { Image } from './components/Image.js';

export class StoryDetailsView extends Component {
  render() {

    const id = this.props.params.id;

    const story = stories.filter(story => story.time === id)[0];

    return (
      <div>
          <Navigation />
          <div className="story-detail">
            <div className="story-detail-image">
              <Image src={story.url} />
            </div>
            <div className="story-detail-metadata">
              <div className="story-detail-location">
                <p data-qa="story-location">{story.location}</p>
              </div>
              <div className="story-detail-text">
                  <p data-qa="story-text">{story.text}</p>
              </div>
              <div className="story-detail-hashtag">
                  <p data-qa="story-hashtag">{story.hashtag}</p>
              </div>
            </div>
          </div>
      </div>
    );
  }
}



